:root {
  --primary-color: #cee9ff;
  --secondary-color: #6c0276;
  --tertiary-color: #2b2d42;
}

* {
  box-sizing:border-box;
  margin: 0;
  padding: 0;
}

/* Centering website on the screen */

body {
  color:  var(--tertiary-color);
  font-family:'Times New Roman', Times, serif;
  background-color: rgb(135, 180, 182);
  line-height: 1.5;
}
#page-wrap { 
  position: absolute; 
  left: 50%; 
  width: 800px;
  margin-left: -400px;
}

/*Header styling start*/
header {
padding: 20px 35px;
background-color:  var(--secondary-color);
display: flex;
justify-content: space-between;
flex-wrap: wrap;
top: 0;
z-index: 9999;
}

header h1 {
  font-weight: bold;
  font-size: 36px;
  color: var(--primary-color);
  margin: 0;
  text-shadow: 0 0 10px rgba(0,0,0,0.5);
}

header a {
  text-decoration: none;
  color:  var(--primary-color);
}

.nav-el {
  margin: 7px 0;
}
.nav-el ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  list-style: none;
}

.nav-el ul li a {
  padding: 10px 15px;
  font-weight: lighter;
  font-size: 1.55vw;
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.nav-el ul li a:hover {
  background: var(--primary-color);
  color: var(--secondary-color);
  border-radius: 15px;
  text-shadow: none;
}
p {
      float: right;
      margin-left: 25px;
      font-size: 20px;
      font-style: oblique;
  }


/*Header styling end*/

/* Hero section style start*/

.hero {
  background: rgb(228, 151, 97);
  /* background-image: url(./images/CodeS.png); */
  /* background-size: contain;
  background-repeat:round;
  background-position: center; */
  display: flex;
  /* text-align: right; */
  align-items: flex-end;
  padding: 2% 6%;
  flex-direction: row; 
}

.hero div {
  flex: 0 0 90%;
}

.hero-subtitle {
  text-align: left;
  color:orange;
}

section {
  padding: 25px 55px;
  }
.intro-wrapper {
      text-indent: 15%;
      align-content: space-around;
      
  }
.about-me-wrapper {
  text-indent: 15%;
  align-content: space-around;
}
/* Resize image on portfolio*/
img {
  max-width: 15%;
  height: auto;
}

/* Hero section style end/

/*Footer Styling Start*/
/* add sematic code, box resize and Contact links alignment and positioning */
footer {
padding: 0px 15px;

}

footer h3 {
  text-align: left;
  font-size: 25px;
  color: purple;
}

footer a {
  color:  var(--tertiary-color);
  padding: 20px 35px;
}

/* Align links to the right */
address {
  background-color: slategrey;
  display: flex;
  justify-content:space-evenly;
  flex-wrap: wrap;
}

/* Footer Styling End */

