/* Media Queries Start */
/* Media Query for 980px screen */
@media screen and (max-width: 980px) {
header {
  padding-bottom: 0;
  justify-content: center;
  position: relative;
  }
}
header h1 {
  width: 100%;
  text-align: center;
}

header nav ul {
  margin-top: 20px;
  width: 100%;
  justify-content: center;
}

header nav ul li a {
  font-size: 20px;
}

footer h2, footer div {
  text-align: center;
  width: 100%;
}

.section-title {
  width: 80%;
}

.contact-info iframe {
  flex: 1 100%;
}

/* Media Query for 768px screen */
@media screen and (max-width: 768px) {
  section {
      padding: 30px 15px;
      }
  
  /* Media Query for 575px screen */
  @media screen and (max-width: 575px) {
  .hero-form button {
      width: 100%;
      }
  
  .section-title {
      width: 95%;
  }
  
  .contact-info {
      text-align: center;
  }
  
  .contact-info > * {
      flex: 0 100%;
  }
  }
  };
  
  /* Media Queries End */

/* Hover effect Header - to fade color/hnage color of text once hovering over the project*/
.nav-effect a:hover { 
  color: var(--tertiary-color);
  font-weight: bold;
}

/* Hover effect for Work Projects - to fade color/hnage color of text once hovering over the project*/
.work-container div:hover { 
  mix-blend-mode:lighten;
  text-shadow: 0px 0px 20px var(--secondary-color);
  transform: scale(1.5);
  margin-right: calc(5% + 10px);
  margin-left: 5%;
}

/* Footer hover start - to fade color/hnage color of text once hovering over the project*/
.footer a:hover {
  color: var(--primary-color);
  font-weight: bold;
  text-shadow: 0px 0px 20px var(--secondary-color);
};
/* Footer Hover end */
